<template>
    <div class="banner_wrap">
        <el-carousel height="4rem">
            <el-carousel-item v-for="(item) in bannerList" :key="item.id">
                <router-link :to="{path:'/detail',query:{url:item.url}}">
                    <img :src="item.pic" :alt="item.title"/>
                </router-link>
            </el-carousel-item>
        </el-carousel>
    </div>
</template>

<script>
	import {reactive, toRefs, inject, onMounted} from '@vue/composition-api';

	export default {
		name: 'Banner',
		setup(props, {root}){
			const state = reactive({
				activeIndex: 0,
				bannerList: inject('bannerList'),
			});

			return {
				...toRefs(state)
			}
		}
	}
</script>

<style scoped>
    .banner_wrap {
        height: 4rem;
    }

    .banner_wrap img {
        width: 100%;
        height: 100%;
    }

    /deep/ .el-carousel__button {
        width: 15px;
    }
</style>