<template>
    <div class="itemlist_wrap">
        <ul>
            <li v-for="(item,index) in list" :key="item.id" @click="toDetailPage(item.url)">
                <span>{{(index + 1 + '').padStart(3,'0')}}、{{item.title}}</span>
                <span>{{item.shiren}}</span>
            </li>
        </ul>
    </div>
</template>

<script>
	import {reactive, toRefs} from '@vue/composition-api';
	
	export default {
		name: 'CategoryItemList',
		setup(props, {root}){
			const state = reactive({
				list: []
			});
			let loadingInstance = root.$loading();
			const {type, name} = root.$route.query;
			root.$http.get(`/index.php?m=Home&c=Api&a=get_fenlei_shici_list&type=${type}&name=${name}`)
				.then(res => {
					const data = res.data.list;
					data.map(item => {
						item.title = item.title.replace('##', '·').replace('**','').replace('$1','').replace('$', '');
						item.url = `http://ap.shicizhongguo.cn/index.php?m=Home&c=Index&a=shici_info&id=${item.id}`;
						return item;
					});
					state.list = data;
					root.$nextTick(() => loadingInstance.close());
				});
			const toDetailPage = (url) => {
                root.$router.push({
                    path: '/detail',
                    query: {url},
                });
            };

			return {
				...toRefs(state),
				toDetailPage
			}
		}
	}
</script>

<style scoped>
    .itemlist_wrap li {
        display: flex;
        justify-content: center;
        padding: 0 0.4rem;
        font-size: 0.28rem;
        line-height: 0.70rem;
        list-style: none;
        letter-spacing: 2px;
    }

    .itemlist_wrap li:nth-child(odd){
        background-color: #f6f6f6;
    }

    .itemlist_wrap li span {
        flex-grow: 1;
        display: block;
        white-space: nowrap;
        overflow: hidden;
        text-overflow: ellipsis;
        max-width: 65%;
    }

    .itemlist_wrap li span:last-child {
        text-align: right;
    }
</style>