<template>
    <div class="category_wrap">
        <div class="category_box" v-for="item in shiciArray" :key="item.type" v-show="$route.name !== 'CategoryItemList'">
            <el-divider content-position="left">{{$route.meta.title}}·{{item.name}}</el-divider>
            <div class="tag_wrap">
                <el-tag type="success" v-for="listItem in item.list"
                        @click="getShiciFenlei(item, listItem)"
                        :key="listItem.id">{{listItem.name}}</el-tag>
            </div>
            <div v-if="item.subItemName && item.fenleiList">
                <el-divider content-position="center">{{item.name}}·{{item.subItemName}}</el-divider>
                <div class="tag_wrap">
                    <el-tag type="info" v-for="fenleiItem in item.fenleiList" :key="fenleiItem.name">
                        <router-link :to="{path:'/category/list',query:{type:item.type,name:fenleiItem.name}}">
                            {{fenleiItem.name}}
                        </router-link>
                    </el-tag>
                </div>
            </div>
        </div>
        <router-view v-show="$route.name === 'CategoryItemList'"></router-view>
    </div>
</template>

<script>
	import {reactive, toRefs} from '@vue/composition-api';

	export default {
		name: 'Category',
		setup(props, {root}){
			const state = reactive({
                shiciArray: [],
                meta: root.$route.meta,
			});
			// shiciArray
            (function(){
	            let loadingInstance = root.$loading();
	            const getShiciArray = () => {
		            root.$http.get('/index.php?m=Home&c=Api&a=get_shicidaquan').then(res => {
			            const {data} = res;
			            window.sessionStorage.setItem('shiciArray', JSON.stringify(data.list));
			            state.shiciArray = data.list;
			            root.$nextTick(() => loadingInstance.close());
		            });
	            };
	            const shiciArrayStr = window.sessionStorage.getItem('shiciArray');
	            if(!shiciArrayStr){
		            getShiciArray();
	            }else{
		            state.shiciArray = JSON.parse(shiciArrayStr);
		            root.$nextTick(() => loadingInstance.close());
	            }
            })();
            const getShiciFenlei = (item, listItem) => {
	            let loadingInstance = root.$loading();
				root.$set(item, 'subItemName', listItem.name);
                root.$http.get(`/index.php?m=Home&c=Api&a=get_shici_fenlei&id=${listItem.id}&type=${item.type}`)
                    .then(res => {
                    	if(item.type === 'chaodai'){
		                    root.$set(item, 'fenleiList', res.data.list.zuopinji_list);
	                    }else{
		                    root.$set(item, 'fenleiList', res.data.list);
                        }
	                    root.$nextTick(() => loadingInstance.close());
                    });
            };
			return {
				...toRefs(state),
                getShiciFenlei,
			}
		}
	}
</script>

<style scoped>
    .category_box {
        padding: 0 0.4rem;
        overflow: hidden;
    }

    /deep/ .el-divider__text{
        font-size: 0.28rem;
        letter-spacing: 2px;
        white-space: nowrap;
    }

    /deep/ .el-divider--horizontal {
        margin: 0.48rem 0;
    }

    .tag_wrap {
        display: flex;
        justify-content: space-between;
        flex-wrap: wrap;
        width: calc(100% + 0.2rem);
        margin-left: -0.2rem;
        margin-top: -0.48rem;
    }

    /deep/ .el-tag {
        margin-top: 0.48rem;
        margin-left: 0.2rem;
        flex-grow: 1;
        text-align: center;
        letter-spacing: 2px;
    }

    /deep/ .el-tag a {
        color: inherit;
        text-decoration: none;
    }
</style>