<template>
    <div class="detail_wrap" v-loading="loading">
        <i class="el-icon-arrow-left" @click="backHandle" v-show="!loading"></i>
        <link rel="stylesheet" href="http://ap.shicizhongguo.cn/Public/home/css/style.css">
        <div class="message_content" v-html="detailHtml"></div>
    </div>
</template>

<script>
	import {reactive, toRefs, inject, onMounted} from '@vue/composition-api';

	export default {
		name: 'Detail',
        setup(props, {root}){
			const state = reactive({
                detailHtml: '',
                loading: true,
            });
			const url = ('' + root.$route.query.url).replace(/(http:\/\/ap.shicizhongguo.cn)/gi, '');
			root.$http.get(url).then(res => {
		        const {data} = res;
		        const $ = root.$cheerio.load(data);
		        let html = $('.message_content').html().trim()
		        html = html.replace(/src="(.*?)"/gi, ($1, $2) => {
		        	if($2.includes('http') || $2.includes('https')){
		        	    return $1;
		        	}
			        return `src="http://ap.shicizhongguo.cn/${$2}"`;
		        });
		        state.detailHtml = html;
		        state.loading = false;
	        });

	        onMounted(() => {
	        	document.documentElement.removeAttribute('style');
            });

	        const backHandle = () => {
	        	root.$router.push('/home');
            };

            return {
                ...toRefs(state),
                backHandle
            }
        }
	}
</script>

<style scoped>
    .detail_wrap {
        position: relative;
        padding-top: 25px;
        min-height: 100vh;
        background: url('../assets/images/message_bg.png') repeat center center;
    }

    .el-icon-arrow-left {
        position: absolute;
        left: 10px;
        top: 10px;
        font-size: 30px;
        color: #38f;
    }
</style>