<template>
    <div class="homepage_wrap">
        <Banner></Banner>
        <div class="img_wrap">
            <img src="@/assets/images/shici.png" style="width: 4rem;" alt=""/>
        </div>
    </div>
</template>

<script>
	import {reactive, toRefs, provide} from '@vue/composition-api';
	import Banner from './Banner';

	export default {
		name: 'HomePage',
		setup(props, {root}){
			const state = reactive({
                bannerList: []
            });
			// bannerList
            (function(){
	            let loadingInstance = root.$loading();
	            const bannerListStr = window.sessionStorage.getItem('bannerList');
	            if(!bannerListStr){
		            root.$http.get('/index.php?m=Home&c=Api&a=lb_jiaodian&type=1').then(res => {
			            const {data} = res;
			            data.list.map(item => {
			            	if(!item.pic.includes('http') || !item.includes('https')){
			            	    item.pic = 'http://ap.shicizhongguo.cn' + item.pic;
			            	}
			            	return item;
                        });
			            window.sessionStorage.setItem('bannerList', JSON.stringify(data.list));
			            state.bannerList = data.list;
			            root.$nextTick(() => loadingInstance.close());
		            });
	            }else{
		            state.bannerList = JSON.parse(bannerListStr);
		            root.$nextTick(() => loadingInstance.close());
	            }
            })();
            provide('bannerList', state.bannerList); // 向组件传值
			return {
                ...toRefs(state)
            }
        },
        components: {
	        Banner
        }
	}
</script>

<style scoped>
    .homepage_wrap {
        height: calc(100% - 1.2rem);
    }

    .img_wrap {
        display: flex;
        justify-content: center;
        align-items: center;
        height: calc(100% - 4rem);
    }
</style>